import Vue from 'vue';
import $ from 'jquery';
import support from '../../store/support.json';
export default {
  props: [],
  components: {},
  data: function data() {
    return {
      npmreactlogin_login: '',
      npmreactlogin_password: '',
      j_captcha_response: '',
      chapchablob: '',
      loading: false,
      valid: false,
      pinCode: '',
      isSigned: window.themeDisplay ? window.themeDisplay.isSignedIn() : false
    };
  },
  computed: {},
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var vm = this;
      $('body').addClass('body_login');
      var current = vm.$router.history.current;
      var currentQuery = current.query;
      vm.makeImageCap();
    });
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {});
  },
  watch: {},
  methods: {
    makeImageCap: function makeImageCap() {
      var vm = this;
      vm.chapchablob = '';
      vm.$store.dispatch('makeImageCapLogin').then(function (result) {
        vm.chapchablob = result;
      }).catch(function (reject) {
        vm.chapchablob = '';
      });
    },
    submitConfirmLogin: function submitConfirmLogin() {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var currentParams = vm.$router.history.current.params;
      var filter = {
        npmreactlogin_login: vm.npmreactlogin_login,
        npmreactlogin_password: vm.npmreactlogin_password,
        j_captcha_response: vm.j_captcha_response
      };

      if (vm.npmreactlogin_login && vm.npmreactlogin_password) {
        vm.$store.dispatch('goToDangNhap', filter);
      }
    },
    doLogOut: function doLogOut() {
      window.location.href = "/c/portal/logout";
    },
    getPassword: function getPassword() {
      var vm = this;
      window.location.href = window.themeDisplay ? window.themeDisplay.getLayoutURL() + '/#/cap-lai-mat-khau' : '';
    },
    goBack: function goBack() {
      window.history.back();
    }
  }
};