import Vue from 'vue';
import $ from 'jquery';
import axios from 'axios';
import support from '../../store/support.json';
import toastr from 'toastr';
import Captcha from './Captcha.vue';
Vue.use(toastr);
export default {
  props: [],
  components: {
    'captcha': Captcha
  },
  data: function data() {
    return {
      loading: false,
      valid: false,
      validResend: false,
      pinCode: '',
      userName: '',
      passWord: '',
      dialogVerify: false,
      dialogPending: false,
      xacthuc_credit: false,
      resend_mail: false,
      dialogResend: false
    };
  },
  computed: {},
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var currentParams = vm.$router.history.current.params;

      if (currentQuery.hasOwnProperty('active_code') && currentQuery.active_code) {
        vm.dialogVerify = true;
        vm.pinCode = currentQuery.active_code;
        setTimeout(function () {
          vm.submitConfirmPIN();
        }, 300);
      }

      try {
        if (xacthuc_credit) {
          vm.xacthuc_credit = true;
        }
      } catch (error) {}

      try {
        if (allow_resend_mail) {
          vm.resend_mail = allow_resend_mail;
        }
      } catch (error) {}
    });
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {});
  },
  watch: {},
  methods: {
    submitConfirmPIN: function submitConfirmPIN() {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var currentParams = vm.$router.history.current.params;
      var dataForm = {
        userId: currentQuery.hasOwnProperty('active_user_id') ? currentQuery.active_user_id : '',
        pinCode: vm.pinCode // console.log('dataForm', dataForm)

      };

      if (vm.$refs.form.validate() && dataForm.userId) {
        vm.loading = true;
        var filter = dataForm;

        if (vm.xacthuc_credit) {
          vm.$store.dispatch('confirmPINBXD', filter).then(function (result) {
            vm.loading = false;
            vm.dialogPending = true;
          }).catch(function (reject) {
            vm.loading = false;
          });
        } else {
          vm.$store.dispatch('confirmPIN', filter).then(function (result) {
            vm.loading = false;
            $('.login-input input[type=text]').val('');
            $('.login-input input[type=password]').val('');
            $('.login-input input[type=text]').val(result.email);
            $('.login-input input[type=password]').val(result.token);
            vm.userName = result.email;
            vm.passWord = result.token;
            vm.goToDangNhap();
          }).catch(function (reject) {
            vm.loading = false;
          });
        }
      }
    },
    resendMail: function resendMail() {
      var vm = this;
      vm.$refs.captcha_resend.makeImageCap();
      vm.dialogResend = true;
    },
    submitResend: function submitResend() {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var currentParams = vm.$router.history.current.params;

      if (vm.$refs.formCaptcha.validate()) {
        var filter = {
          activeId: currentQuery.hasOwnProperty('active_user_id') ? currentQuery.active_user_id : '',
          notificationType: 'APPLICANT-01',
          notifyMessage: vm.$refs.captcha_resend.j_captcha_response
        };
        vm.$store.dispatch('resendMail', filter).then(function (result) {
          toastr.success('Mã PIN đã được gửi lại. Vui lòng kiểm tra email.');
          vm.dialogResend = false;
          vm.$refs.captcha.makeImageCap();
          vm.pinCode = '';
        }).catch(function (error) {
          vm.$refs.captcha_resend.makeImageCap();
        });
      }
    },
    cancelResend: function cancelResend() {
      var vm = this;
      vm.dialogResend = false;
    },
    goToDangNhap: function goToDangNhap() {
      var vm = this;
      var configs = {
        headers: {// 'Authorization': 'BASIC ' + window.btoa(filter['npmreactlogin_login'] + ":" + filter['npmreactlogin_password']),
        }
      };
      var dataPostApplicant = new URLSearchParams();
      dataPostApplicant.append('Authorization', 'BASIC ' + window.btoa(vm.userName + ':' + vm.passWord));
      axios.post('/o/v1/opencps/login', dataPostApplicant, configs).then(function (response) {
        if (response.data !== '' && response.data !== 'ok' && response.data !== 'captcha' && response.data !== "lockout") {
          if (response.data === 'pending') {
            window.location.href = window.themeDisplay.getURLHome() + '/register#/xac-thuc-tai-khoan?active_user_id=' + window.themeDisplay.getUserId() + '&redirectURL=' + window.themeDisplay.getURLHome();
          } else {
            window.location.href = response.data;
          }
        } else if (response.data === 'ok') {
          window.location.href = window.themeDisplay.getURLHome();
        } else if (response.data === 'captcha') {
          var redirectURL = themeDisplay.getLayoutRelativeURL().substring(0, themeDisplay.getLayoutRelativeURL().lastIndexOf('/'));

          if (redirectURL !== '') {
            window.location.href = redirectURL + '/register#/login';
          } else {
            window.location.href = themeDisplay.getURLHome() + '/register#/login';
          }
        } else if (response.data === "lockout") {
          toastr.error("Bạn đã đăng nhập sai quá 5 lần. Tài khoản bị tạm khóa trong 10 phút.");
        } else {}
      }).catch(function (error) {});
    },
    goBack: function goBack() {
      window.history.back();
    }
  }
};