import Vue from 'vue';
import $ from 'jquery';
import support from '../../store/support.json';
import Captcha from './Captcha.vue';
import axios from 'axios';
export default {
  props: [],
  components: {
    'captcha': Captcha
  },
  data: function data() {
    return {
      loading: false,
      valid: false,
      confirmCode: '',
      dialogConfirm: false,
      userIdLogin: ''
    };
  },
  computed: {
    pathNameConfig: function pathNameConfig() {
      return this.$store.getters.getPathNameConfig;
    }
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var vm = this;
      var current = vm.$router.history.current;
      var currentQuery = current.query;
    });
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {});
  },
  watch: {},
  methods: {
    submitConfirmPASS: function submitConfirmPASS() {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var currentParams = vm.$router.history.current.params;

      if (vm.$refs.form.validate()) {
        vm.loading = true;
        var configs = {
          headers: {// 'Authorization': 'BASIC ' + window.btoa(vm.confirmCode + ":0")
          }
        };
        var dataPostApplicant = new URLSearchParams();
        dataPostApplicant.append('Authorization', 'BASIC ' + window.btoa(vm.confirmCode + ":0"));
        axios.post('/o/v1/opencps/login', dataPostApplicant, configs).then(function (response) {
          if (response.data !== '' && response.data !== 'ok' && response.data !== 'captcha' && response.data !== "lockout" && (response.data === 'pending' || response.data === 'verify')) {
            vm.loading = false;
            vm.$refs.captcha.makeImageCap();
            vm.dialogConfirm = true;
            var idUser = '';

            if (response.data === 'pending' && response.headers.hasOwnProperty('userid')) {
              idUser = response.headers.userid;
            }

            if (response.data === 'verify' && response.headers.hasOwnProperty('applicantid')) {
              idUser = response.headers.applicantid;
            }

            vm.userIdLogin = idUser;
          } else {
            var filter = {
              confirmCode: vm.confirmCode,
              j_captcha_response: vm.$refs.captcha.j_captcha_response
            };
            vm.$store.dispatch('confirmForgotPASS', filter).then(function (result) {
              vm.loading = false;
              vm.$router.push({
                path: '/xac-thuc-cap-lai-mat-khau?active_user_id=' + vm.confirmCode
              });
            }).catch(function (reject) {
              vm.loading = false;
              vm.$refs.captcha.makeImageCap();
            });
          }
        }).catch(function () {
          var filter = {
            confirmCode: vm.confirmCode,
            j_captcha_response: vm.$refs.captcha.j_captcha_response
          };
          vm.$store.dispatch('confirmForgotPASS', filter).then(function (result) {
            vm.loading = false;
            vm.$router.push({
              path: '/xac-thuc-cap-lai-mat-khau?active_user_id=' + vm.confirmCode
            });
          }).catch(function (reject) {
            vm.loading = false;
            vm.$refs.captcha.makeImageCap();
          });
        });
      }
    },
    confirmXacThuc: function confirmXacThuc() {
      var vm = this;
      var url = window.themeDisplay.getSiteAdminURL().split('/~/')[0].replace('group', 'web');
      window.location.href = url + vm.pathNameConfig + "#/xac-thuc-tai-khoan?active_user_id=" + vm.userIdLogin + "&redirectURL=" + url;
    },
    goBack: function goBack() {
      window.history.back();
    }
  }
};