import Vue from 'vue';
import $ from 'jquery';
import support from '../../store/support.json';
import Captcha from './Captcha.vue';
import toastr from 'toastr';
Vue.use(toastr);
export default {
  props: [],
  components: {
    'captcha': Captcha
  },
  data: function data() {
    return {
      loading: false,
      valid: false,
      confirmCode: '',
      resend_mail: false,
      dialogResend: false,
      validResend: false
    };
  },
  computed: {},
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var vm = this;
      var current = vm.$router.history.current;
      var currentQuery = current.query;

      try {
        if (allow_resend_mail) {
          vm.resend_mail = allow_resend_mail;
        }
      } catch (error) {}
    });
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {});
  },
  watch: {},
  methods: {
    submitConfirmPASS: function submitConfirmPASS() {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var currentParams = vm.$router.history.current.params;
      var dataForm = {
        userCode: currentQuery.hasOwnProperty('active_user_id') ? currentQuery.active_user_id : '',
        confirmCode: vm.confirmCode,
        j_captcha_response: vm.$refs.captcha.j_captcha_response
      };

      if (vm.$refs.form.validate()) {
        vm.loading = true;
        var filter = dataForm;
        vm.$store.dispatch('confirmCodeForgotPass', filter).then(function (result) {
          vm.loading = false;

          if (result === 'captcha') {
            vm.$refs.captcha.makeImageCap();
          }
        }).catch(function (reject) {
          vm.loading = false;
          vm.$refs.captcha.makeImageCap();
        });
      }
    },
    resendMail: function resendMail() {
      var vm = this;
      vm.$refs.captcha_resend.makeImageCap();
      vm.dialogResend = true;
    },
    cancelResend: function cancelResend() {
      var vm = this;
      vm.dialogResend = false;
    },
    submitResend: function submitResend() {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var currentParams = vm.$router.history.current.params;
      var filter = {
        activeId: currentQuery.hasOwnProperty('active_user_id') ? currentQuery.active_user_id : '',
        notificationType: 'USER-05',
        notifyMessage: vm.$refs.captcha_resend.j_captcha_response
      };
      vm.$store.dispatch('resendMail', filter).then(function (result) {
        toastr.success('Mã bảo mật đã được gửi lại. Vui lòng kiểm tra email.');
        vm.dialogResend = false;
        vm.$refs.captcha.makeImageCap();
        vm.confirmCode = '';
      }).catch(function () {
        vm.$refs.captcha_resend.makeImageCap();
      });
    },
    goBack: function goBack() {
      window.history.back();
    }
  }
};