import Vue from 'vue';
import $ from 'jquery';
import toastr from 'toastr';
import support from '../../store/support.json';
toastr.options = {
  'closeButton': true,
  'timeOut': '5000'
};
export default {
  props: [],
  components: {},
  data: function data() {
    return {
      loading: false,
      valid: false,
      confirmCode: ''
    };
  },
  computed: {},
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var vm = this;
      var current = vm.$router.history.current;
      var currentQuery = current.query;
      $('#app_login').css('display', 'none !important');
      $('#navigation').css('display', 'none');
    });
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {});
  },
  watch: {},
  methods: {
    submitChangePass: function submitChangePass() {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var currentParams = vm.$router.history.current.params;

      if (vm.$refs.form.validate()) {
        vm.loading = true;
        var filter = {
          oldPassword: vm.oldPassWord,
          newPassword: vm.newPassWord
        };
        vm.$store.dispatch('changePass', filter).then(function (result) {
          vm.loading = false;
          toastr.success("Cập nhật mật khẩu thành công");
          var urlDvc = window.themeDisplay.getSiteAdminURL().split('/~/')[0].replace('group', 'web');
          window.location.href = urlDvc + '/dich-vu-cong';
        }).catch(function (reject) {
          vm.loading = false;
          toastr.error("Cập nhật thất bại. Vui lòng thực hiện lại");
        });
      }
    },
    goBack: function goBack() {
      window.history.back();
    }
  }
};