import Vue from 'vue';
import $ from 'jquery';
import toastr from 'toastr';
import support from '../../store/support.json';
export default {
  props: [],
  components: {},
  data: function data() {
    return {
      npmreactlogin_login: '',
      npmreactlogin_password: '',
      captcha: false,
      j_captcha_response: '',
      chapchablob: '',
      loading: false,
      valid: false,
      pinCode: '',
      isSigned: window.themeDisplay ? window.themeDisplay.isSignedIn() : false,
      conectDvcqg: false,
      dataMapping: '',
      dialogContact: false,
      validContact: false,
      contactEmail: '',
      hasEmail: false,
      rules: {
        required: function required(value) {
          return !!value || 'Email là bắt buộc';
        },
        email: function email(value) {
          var pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || 'Địa chỉ Email không hợp lệ';
        }
      }
    };
  },
  computed: {
    loadingLogin: function loadingLogin() {
      return this.$store.getters.loading;
    }
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var vm = this;
      $('body').addClass('body_login');
      $('#banner .container').css('display', 'none');

      if (typeof Storage !== 'undefined') {
        var count = sessionStorage.getItem('isbot');

        if (window.themeDisplay.isSignedIn() || !count) {
          sessionStorage.setItem('isbot', '0');
        }

        if (Number(sessionStorage.getItem('isbot')) >= 5) {
          vm.captcha = true;
        }
      }

      var current = vm.$router.history.current;
      var currentQuery = current.query;
      vm.makeImageCap();

      try {
        vm.conectDvcqg = ssoConfig ? ssoConfig['active'] : false;
        vm.captcha = hasCaptcha ? hasCaptcha : false;
      } catch (error) {} // 


      var searchParams = window.location.href.split("?")[1];

      if (searchParams) {
        var dataDVCQG = decodeURIComponent(String(vm.getSearchParams(searchParams, "data")));

        if (dataDVCQG) {
          var dataObj = JSON.parse(atob(dataDVCQG));
          vm.dataMapping = dataObj;

          if (dataObj && dataObj.hasOwnProperty('userId') && String(dataObj.userId) === '0') {
            vm.mapping = true;
            var typeAccount = '';
            var title = '';
            var idType = '';

            if (dataObj.LoaiTaiKhoan === '1') {
              typeAccount = 'Số CMND/ Căn cước ';
              idType = dataObj.SoCMND;
            } else if (dataObj.LoaiTaiKhoan === '2') {
              typeAccount = 'Mã số thuế ';
              idType = dataObj.MaSoThue;
            }

            if (typeAccount && (dataObj.SoCMND || dataObj.MaSoThue)) {
              title = typeAccount + idType + ' đã tạo tài khoản trên hệ thống. Vui lòng đăng nhập để đồng bộ với tài khoản Cổng Dịch vụ công Quốc gia.';
            }

            if (title) {
              toastr.options = {
                'positionClass': 'toast-top-center',
                'closeButton': true,
                'timeOut': '30000'
              };
              toastr.info(title);
            }
          }

          if (dataObj && dataObj.hasOwnProperty('state') && dataObj.state === 'create') {
            vm.contactEmail = dataObj['ThuDienTu'] ? dataObj['ThuDienTu'] : '';
            vm.dialogContact = true;
          }
        }
      }
    });
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {});
  },
  mounted: function mounted() {
    var vm = this;
    vm.$nextTick(function () {
      try {
        if (addScrLogin) {
          addScrLogin();
        }
      } catch (error) {}
    });
  },
  watch: {},
  methods: {
    loginDVCQG: function loginDVCQG() {
      var vm = this;
      var filter = {
        state: '',
        redirectURL: window.location.href.split("?")[0]
      };
      vm.$store.dispatch('getVNConect', filter).then(function (result) {
        if (result) {
          window.location.href = result;
        } else {
          alert('Chức năng đang cập nhật');
        }
      }).catch(function () {
        alert('Chức năng đang cập nhật');
      });
    },
    makeImageCap: function makeImageCap() {
      var vm = this;
      vm.chapchablob = '';
      vm.$store.dispatch('makeImageCapLogin').then(function (result) {
        vm.chapchablob = result;
      }).catch(function (reject) {
        vm.chapchablob = '';
      });
    },
    submitConfirmLogin: function submitConfirmLogin() {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var currentParams = vm.$router.history.current.params;
      var filter = {
        npmreactlogin_login: vm.npmreactlogin_login,
        npmreactlogin_password: vm.npmreactlogin_password,
        j_captcha_response: vm.j_captcha_response
      };

      if (vm.$refs.form.validate()) {
        vm.$store.dispatch('goToDangNhap', filter).then(function (result) {
          if (vm.mapping && result === 'success') {
            vm.doMappingDvcqg();
          }

          if (result !== 'success') {
            if (typeof Storage !== 'undefined') {
              var count = Number(sessionStorage.getItem('isbot'));
              count += 1;
              sessionStorage.setItem('isbot', String(count));

              if (count === 5) {
                vm.captcha = true;
                vm.makeImageCap();
              }
            }
          } else {
            if (typeof Storage !== 'undefined') {
              sessionStorage.setItem('isbot', '0');
            }
          }

          if (result === 'captcha') {
            vm.captcha = true;
            vm.makeImageCap();
          }
        }).catch(function () {
          if (typeof Storage !== 'undefined') {
            var count = Number(sessionStorage.getItem('isbot'));
            count += 1;
            sessionStorage.setItem('isbot', String(count));

            if (count === 5) {
              vm.captcha = true;
              vm.makeImageCap();
            }
          }
        });
      }
    },
    doMappingDvcqg: function doMappingDvcqg() {
      var vm = this;
      var filter = {
        dataMapping: vm.dataMapping
      };
      vm.$store.dispatch('mappingDvcqg', filter).then(function (result) {}).catch(function () {});
    },
    submitContact: function submitContact() {
      var vm = this;

      if (vm.$refs.formContact.validate()) {
        // call cập nhật email user
        var oldEmail = vm.dataMapping['ThuDienTu'] ? vm.dataMapping['ThuDienTu'] : vm.dataMapping['TechID'] + '@dvcqg.gov.vn';
        $.ajax({
          url: '/o/rest/v2/dvcqgsso/changeemail?oldEmail=' + oldEmail + '&newEmail=' + vm.contactEmail + '&techId=' + vm.dataMapping['TechID'],
          data: {},
          type: 'POST',
          async: false,
          headers: {
            'groupId': window.themeDisplay.getScopeGroupId(),
            'Token': window.Liferay.authToken
          },
          success: function success(result, status, xhr) {
            vm.dataMapping.state = 'auth';
            vm.doAuth(vm.dataMapping);
            setTimeout(function () {
              var urlDvc = window.themeDisplay.getSiteAdminURL().split('/~/')[0].replace('group', 'web');
              window.location.href = urlDvc;
            }, 100);
          },
          error: function error(xhr) {}
        });
      }
    },
    doAuth: function doAuth(dataIn) {
      $.ajaxSetup({
        headers: {
          'groupId': window.themeDisplay.getScopeGroupId(),
          'Token': window.Liferay.authToken,
          'Content-Type': 'application/json'
        }
      });
      $.post('/o/rest/v2/dvcqgsso/auth', JSON.stringify(dataIn)).done(function () {});
    },
    getSearchParams: function getSearchParams(prams, key) {
      var value = "";
      var headers = prams.split("&");
      headers.forEach(function (header) {
        header = header.split("=");
        var keyHeader = header[0];

        if (keyHeader === key) {
          value = header[1];
        }
      });
      return value;
    },
    cancelContact: function cancelContact() {
      var vm = this;
      vm.dialogContact = false;
      var urlDvc = window.themeDisplay.getSiteAdminURL().split('/~/')[0].replace('group', 'web');
      window.location.href = urlDvc;
    },
    doLogOut: function doLogOut() {
      window.location.href = "/c/portal/logout";
    },
    getPassword: function getPassword() {
      var vm = this;
      window.location.href = window.themeDisplay ? window.themeDisplay.getLayoutURL() + '/#/cap-lai-mat-khau' : '';
    },
    goBack: function goBack() {
      window.history.back();
    }
  }
};